import React from 'react';


const Card = ({name, email, id}) => {
    
    const myUrl = "https://robohash.org/" + id + '?size=300x300'
   return ( 
         <div className='tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5'>
            <img alt = 'robots' src = {myUrl}  />
                <div>
                    <h2> {name} </h2>
                    <p> {email} </p>
                </div>
        </div>
   )    

}

export default Card